export default {
  name: "TransformMedia",
  props: {
    file: {
      type: Object,
      required: true
    },
    src: {
      type: String,
      required: true
    },
    isImage: {
      type: Boolean,
      default: true
    },
    isVideo: {
      type: Boolean,
      default: false
    }
  },
  data() {
    return {
      pointers: new Map(),
      // transform state
      scale: 1,
      rotation: 0,
      tx: 0,
      ty: 0,
      // gesture start snapshots
      startScale: 1,
      startRotation: 0,
      startTx: 0,
      startTy: 0,
      startCenter: null,
      startDist: 0,
      startAngle: 0,
      // drag
      dragging: false,
      dragStart: null,
      viewportRect: null,
      // bounds
      minScale: 1,
      maxScale: 4
    };
  },
  computed: {
    isActiveTransform() {
      return this.scale > 1.001 || this.pointers.size >= 2 || this.dragging;
    },
    mediaStyle() {
      return {
        transform: `translate3d(${this.tx}px, ${this.ty}px, 0) scale(${this.scale}) rotate(${this.rotation}deg)`,
        transition: this.pointers.size ? "none" : "transform 0.25s ease",
        transformOrigin: "center center"
      };
    }
  },
  watch: {
    isActiveTransform(v) {
      this.$emit(v ? "lock" : "unlock");
    }
  },
  methods: {
    onLoad() {
      // 图片加载完成
    },
    reset() {
      this.scale = 1;
      this.rotation = 0;
      this.tx = 0;
      this.ty = 0;
      this.pointers.clear();
      this.dragging = false;
      this.$emit("unlock");
    },
    clamp(v, min, max) {
      return Math.max(min, Math.min(max, v));
    },
    getViewportRect() {
      return this.$refs.viewport?.getBoundingClientRect();
    },
    calcTwoPointer() {
      const pts = Array.from(this.pointers.values());
      const p0 = pts[0],
        p1 = pts[1];
      const dx = p1.x - p0.x;
      const dy = p1.y - p0.y;
      const dist = Math.hypot(dx, dy);
      const angle = Math.atan2(dy, dx) * (180 / Math.PI);
      const center = {
        x: (p0.x + p1.x) / 2,
        y: (p0.y + p1.y) / 2
      };
      return {
        dist,
        angle,
        center
      };
    },
    onPointerDown(e) {
      e.currentTarget.setPointerCapture?.(e.pointerId);
      this.viewportRect = this.getViewportRect();
      this.pointers.set(e.pointerId, {
        x: e.clientX,
        y: e.clientY
      });

      // 2指开始：初始化 pinch/rotate 基准
      if (this.pointers.size === 2) {
        const {
          dist,
          angle,
          center
        } = this.calcTwoPointer();
        this.startDist = dist;
        this.startAngle = angle;
        this.startCenter = center;
        this.startScale = this.scale;
        this.startRotation = this.rotation;
        this.startTx = this.tx;
        this.startTy = this.ty;
        this.dragging = false;
        return;
      }

      // 1指：如果已放大，则进入拖拽
      if (this.scale > 1.001) {
        this.dragging = true;
        this.dragStart = {
          x: e.clientX,
          y: e.clientY
        };
        this.startTx = this.tx;
        this.startTy = this.ty;
      }
    },
    onPointerMove(e) {
      if (!this.pointers.has(e.pointerId)) return;
      this.pointers.set(e.pointerId, {
        x: e.clientX,
        y: e.clientY
      });

      // 2指：缩放 + 旋转
      if (this.pointers.size === 2) {
        e.preventDefault();
        const {
          dist,
          angle,
          center
        } = this.calcTwoPointer();
        const scaleFactor = dist / (this.startDist || dist);
        this.scale = this.clamp(this.startScale * scaleFactor, this.minScale, this.maxScale);
        const deltaAngle = angle - this.startAngle;
        this.rotation = this.startRotation + deltaAngle;

        // 跟随双指中心移动
        if (this.startCenter && this.viewportRect) {
          const cx0 = this.startCenter.x - this.viewportRect.left - this.viewportRect.width / 2;
          const cy0 = this.startCenter.y - this.viewportRect.top - this.viewportRect.height / 2;
          const cx1 = center.x - this.viewportRect.left - this.viewportRect.width / 2;
          const cy1 = center.y - this.viewportRect.top - this.viewportRect.height / 2;
          this.tx = this.startTx + (cx1 - cx0);
          this.ty = this.startTy + (cy1 - cy0);
        }
        return;
      }

      // 1指：拖拽（只在 scale>1 时）
      if (this.dragging && this.scale > 1.001) {
        e.preventDefault();
        const dx = e.clientX - this.dragStart.x;
        const dy = e.clientY - this.dragStart.y;
        this.tx = this.startTx + dx;
        this.ty = this.startTy + dy;
      }
    },
    onPointerUp(e) {
      if (this.pointers.has(e.pointerId)) this.pointers.delete(e.pointerId);
      if (this.pointers.size < 2) {
        this.startCenter = null;
        this.startDist = 0;
        this.startAngle = 0;
      }
      if (this.pointers.size === 0) {
        this.dragging = false;
        // 缩放回到1附近，自动归位
        if (this.scale <= 1.001) {
          this.scale = 1;
          this.rotation = this.snapRotation(this.rotation);
          this.tx = 0;
          this.ty = 0;
        }
      }
    },
    snapRotation(deg) {
      const snapped = Math.round(deg / 90) * 90;
      return (snapped % 360 + 360) % 360;
    },
    onDblClick() {
      if (this.scale > 1.001) {
        this.scale = 1;
        this.tx = 0;
        this.ty = 0;
        this.rotation = this.snapRotation(this.rotation);
      } else {
        this.scale = 2;
      }
    }
  }
};